import autotime from '@/utils/autotime';
import request from '@/utils/request';
var viewIndex = {
  pv: '浏览量',
  uv: '访客数',
  pv_d_uv: '人均浏览量',
  pv_collect: '收藏小程序'
};
var orderIndex = {
  pv_cart: '加购商品数',
  pv_order: '订单数',
  uv_order: '下单买家数',
  gmv: 'GMV'
};
var payIndex = {
  gmv_pay: '含券支付金额',
  pv_pay: '支付订单数',
  uv_pay: '支付买家数',
  gmv_fee: '支付金额',
  gmv_d_uv_pay: '客单价',
  pv_cp: '自有券核销',
  pv_wxcp: '微信券核销',
  pv_pay_cc: '退款订单数',
  uv_pay_cc: '退款买家数',
  gmv_fee_cc: '退款金额'
};
var payLastIndex = {
  gmv_last: '含券支付金额',
  pv_pay_last: '支付订单数',
  uv_pay_last: '支付买家数',
  gmv_fee_last: '支付金额',
  gmv_last_d_uv_pay: '客单价',
  pv_cp_last: '自有券核销',
  pv_wxcp_last: '微信券核销',
  pv_pay_cc: '退款订单数',
  uv_pay_cc: '退款买家数',
  gmv_fee_cc: '退款金额'
};
export default {
  data: function data() {
    return {
      indices: ['pv', 'uv', 'pv_share', 'pv_collect', 'pv_cart', 'pv_order', 'uv_order', 'gmv', 'pv_pay', 'uv_pay', 'gmv_fee', 'gmv_pay', 'pv_cp', 'pv_wxcp', 'gmv_last', 'pv_pay_last', 'uv_pay_last', 'pv_cp_last', 'pv_wxcp_last', 'pv_pay_cc', 'uv_pay_cc', 'gmv_fee_last', 'gmv_fee_cc'],
      view: viewIndex,
      order: orderIndex,
      pay: payIndex,
      loading: false,
      now: {},
      pre: {},
      rate: {},
      nop: 'all',
      dates: [],
      includeCancel: '0',
      pickerOptions: {
        shortcuts: [{
          text: '昨天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.yday, autotime.yday]);
          }
        }, {
          text: '7天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.y6day, autotime.date]);
          }
        }, {
          text: '30天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.y29day, autotime.date]);
          }
        }]
      }
    };
  },
  computed: {
    rateVp: function rateVp() {
      return Math.round(this.now.uv_pay / this.now.uv * 10000 || 0) / 100;
    },
    rateOp: function rateOp() {
      return Math.round(this.now.uv_pay / this.now.uv_order * 10000 || 0) / 100;
    },
    rateVo: function rateVo() {
      return Math.round(this.now.uv_order / this.now.uv * 10000 || 0) / 100;
    }
  },
  created: function created() {
    autotime.reloadTime();
    this.dates = [autotime.date, autotime.date];
    this.getOverView();
  },
  methods: {
    selectDates: function selectDates(e) {
      this.getOverView();
    },
    switchCancel: function switchCancel(v) {
      if (this.includeCancel === '0') {
        this.pay = payIndex;
      } else {
        this.pay = payLastIndex;
      }
    },
    getOverView: function getOverView() {
      var _this = this;

      this.loading = true;
      request({
        url: '/app/business/appIndex',
        method: 'post',
        data: {
          gid: this.nop,
          cmp_period: true,
          indices: this.indices,
          sts: this.dates[0] / 1000,
          ets: this.dates[1] / 1000 + 86400
        }
      }).then(function (res) {
        _this.loading = false;
        _this.now = autotime.updateIndiceRate(res.data);
        _this.pre = autotime.updateIndiceRate(res.pre);

        _this.indices.forEach(function (idx) {
          if (!_this.now[idx]) _this.now[idx] = 0;
          if (!_this.pre[idx]) _this.pre[idx] = 0;
          _this.rate[idx] = autotime.cmpWithPoint2(_this.now[idx], _this.pre[idx]);
        });

        _this.rate.pv_d_uv = autotime.cmpWithPoint2(_this.now.pv_d_uv, _this.pre.pv_d_uv);
        _this.rate.gmv_d_uv_pay = autotime.cmpWithPoint2(_this.now.gmv_d_uv_pay, _this.pre.gmv_d_uv_pay);
        _this.rate.gmv_last_d_uv_pay = autotime.cmpWithPoint2(_this.now.gmv_last_d_uv_pay, _this.pre.gmv_d_uv_pay_last);
      }).catch(function (err) {
        _this.loading = false;
        console.log(err);
      });
    }
  }
};