import echarts from 'echarts';
import { debounce } from '@/utils';
import autotime from '@/utils/autotime';
import request from '@/utils/request';

var moment = require('moment');

export default {
  data: function data() {
    return {
      downloadLoading: false,
      loading: false,
      indices: {
        uv: '访客数',
        pv_pay: '支付订单数',
        uv_pay: '支付买家数',
        gmv: 'GMV',
        pv_cp: '自有券核销',
        pv_wxcp: '微信券核销',
        gmv_fee: '支付金额'
      },
      hdata: {},
      ddata: {},
      times: new Date(),
      index: 'uv',
      isAccumlate: '',
      chart: null,
      sidebarElm: null,
      flag: true
    };
  },
  created: function created() {
    autotime.reloadTime();
    this.times = autotime.date;
  },
  mounted: function mounted() {
    var _this = this;

    this.chart = echarts.init(this.$refs.chart);
    this.__resizeHandler = debounce(function () {
      if (_this.chart) {
        _this.chart.resize();
      }
    }, 100);
    window.addEventListener('resize', this.__resizeHandler); // 监听侧边栏的变化

    this.sidebarElm = document.getElementsByClassName('sidebar-container')[0];
    this.sidebarElm && this.sidebarElm.addEventListener('transitionend', this.sidebarResizeHandler);
    this.updateChart();
  },
  beforeDestroy: function beforeDestroy() {
    if (this.chart) {
      this.chart.dispose();
      this.chart = null;
    }

    window.removeEventListener('resize', this.__resizeHandler);
    this.sidebarElm && this.sidebarElm.removeEventListener('transitionend', this.sidebarResizeHandler);
  },
  methods: {
    sidebarResizeHandler: function sidebarResizeHandler(e) {
      if (e.propertyName === 'width') {
        this.__resizeHandler();
      }
    },
    setOptions2: function setOptions2(ddata, dates) {
      this.chart.clear();
      var seriesDates = [{
        name: '最近七天',
        itemStyle: {
          normal: {
            color: '#3888fa',
            lineStyle: {
              color: '#3888fa',
              width: 2
            }
          }
        },
        smooth: true,
        type: 'bar',
        data: ddata,
        animationDuration: 900,
        animationEasing: 'cubicInOut'
      }];
      this.chart.setOption({
        color: ['#f4e0c6', '#efb365', '#EB8705', '#d48265', '#91c7ae'],
        title: {
          text: this.indices[this.index],
          textStyle: {
            fontSize: 16,
            color: '#404040',
            fontWeight: 400
          }
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 60,
          containLabel: true
        },
        xAxis: {
          data: dates,
          boundaryGap: true,
          axisTick: {
            show: false
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          }
        },
        legend: {
          data: ['最近七天'],
          right: 50
        },
        series: seriesDates
      });
    },
    setOptions: function setOptions(reference, today) {
      this.chart.clear();
      var series = [{
        name: '选择日',
        itemStyle: {
          normal: {
            color: 'red',
            lineStyle: {
              color: 'red',
              width: 2
            }
          }
        },
        smooth: true,
        type: 'line',
        data: today,
        animationDuration: 2800,
        animationEasing: 'cubicInOut'
      }, {
        name: '前一日',
        smooth: true,
        type: 'line',
        itemStyle: {
          normal: {
            color: '#3888fa',
            lineStyle: {
              color: '#3888fa',
              width: 2,
              type: 'dashed'
            },
            areaStyle: {
              color: '#f3f8ff'
            }
          }
        },
        data: reference,
        animationDuration: 2800,
        animationEasing: 'quadraticOut'
      }];
      this.chart.setOption({
        color: ['#f4e0c6', '#efb365', '#EB8705', '#d48265', '#91c7ae'],
        title: {
          text: this.indices[this.index],
          textStyle: {
            fontSize: 16,
            color: '#404040',
            fontWeight: 400
          }
        },
        grid: {
          left: 20,
          right: 20,
          bottom: 20,
          top: 60,
          containLabel: true
        },
        xAxis: {
          data: autotime.hours,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          }
        },
        legend: {
          data: ['选择日', '前一日'],
          right: 50
        },
        series: series
      });
    },
    reset: function reset() {
      this.times = autotime.date;
      this.updateChart();
    },
    updateChart: function updateChart() {
      var _this2 = this;

      if (this.isAccumlate) {
        var dates = autotime.formatPreDays(this.times, 7);
        var vals = [];
        dates.forEach(function (d) {
          if (_this2.ddata[d]) {
            vals.push(_this2.ddata[d][_this2.index] || 0);
          }
        });

        if (vals.length === dates.length) {
          this.setOptions2(vals, dates);
        } else {
          this.loading = true;
          request({
            url: '/app/business/appIndex',
            method: 'post',
            data: {
              gid: 'all',
              time_group: 'ds',
              indices: Object.keys(this.indices),
              sts: this.times / 1000 - 6 * 86400,
              ets: this.times / 1000 + 86400
            }
          }).then(function (res) {
            _this2.loading = false;
            dates.forEach(function (d) {
              _this2.ddata[d] = res.data[d] || {};
            });

            _this2.updateChart();
          }).catch(function (e) {
            _this2.loading = false;
          });
        }
      } else {
        var dstr = moment(this.times).format('YYYY-MM-DD');
        var pdstr = moment(new Date(this.times.getTime() - 86400000)).format('YYYY-MM-DD');
        console.log(this.hdata);

        if (this.hdata[dstr] && this.hdata[pdstr]) {
          var _vals = [];
          var pre = [];
          autotime.hours.forEach(function (h, i) {
            _vals.push(_this2.hdata[dstr][i.toString()] && _this2.hdata[dstr][i.toString()][_this2.index] || 0);

            pre.push(_this2.hdata[pdstr][i.toString()] && _this2.hdata[pdstr][i.toString()][_this2.index] || 0);
          });
          this.setOptions(pre, _vals);
        } else {
          var sts = this.times.getTime() / 1000 - 86400;
          var ets = this.times.getTime() / 1000 + 86400; // if (this.hdata[dstr]) {
          //   ets = ets - 86400
          // } else if (this.hdata[pdstr]) {
          //   sts = sts + 86400
          // }

          this.loading = true;
          request({
            url: '/app/business/appIndex',
            method: 'post',
            data: {
              gid: 'all',
              time_group: 'hh',
              indices: Object.keys(this.indices),
              sts: sts,
              ets: ets
            }
          }).then(function (res) {
            var vals = [];
            var pre = [];
            _this2.loading = false;
            if (res.data[pdstr]) _this2.hdata[pdstr] = res.data[pdstr];
            if (res.data[dstr]) _this2.hdata[dstr] = res.data[dstr];
            autotime.hours.forEach(function (h, i) {
              vals.push(_this2.hdata[dstr][i.toString()] && _this2.hdata[dstr][i.toString()][_this2.index] || 0);
              pre.push(_this2.hdata[pdstr][i.toString()] && _this2.hdata[pdstr][i.toString()][_this2.index] || 0);
            });

            _this2.setOptions(pre, vals);

            console.log('zzzyyy');
          }).catch(function (e) {
            _this2.loading = false;
          });
        }
      }
    }
  }
};